:root {
  --plyr-color-main: #F08322;
}

html {
  font-family: "TT Hoves", sans-serif;
  line-height: 1.42;
  font-size: 16px;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  height: 100%;
  color: #FFF;
  font-family: "TT Hoves", sans-serif;
  font-weight: 400;
  font-size: 18px;
  font-size: 1.125rem;
  line-height: 1.45;
  background: #222E3F;
}
@media (max-width: 1259px) {
  body {
    font-size: 16px;
    font-size: 1rem;
  }
}
@media (max-width: 599px) {
  body {
    font-size: 14px;
    font-size: 0.875rem;
  }
}

#wrapper {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  min-height: 100%;
  min-width: 100%;
}

#middle {
  -webkit-flex: 1 0 auto;
  -moz-flex: 1 0 auto;
  -o-flex: 1 0 auto;
  -ms-flex: 1 0 auto;
  flex: 1 0 auto;
  padding-bottom: 30px;
}
@media (max-width: 959px) {
  #middle {
    padding-bottom: 10px;
  }
}

#footer {
  -webkit-flex: 0 0 auto;
  -moz-flex: 0 0 auto;
  -o-flex: 0 0 auto;
  -ms-flex: 0 0 auto;
  flex: 0 0 auto;
  width: 100%;
}

p {
  margin: 0px 0px 32px;
}

ul, ol {
  padding: 0px;
  margin: 0px;
  list-style: none;
}

li {
  list-style: none;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6, .h20 {
  font-family: "TT Hoves";
  font-weight: 400;
  color: #FFF;
  margin: 40px 0px 40px;
  text-transform: none;
}
@media (max-width: 959px) {
  h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6, .h20 {
    margin: 24px 0 16px;
  }
}

.ui-h-extra {
  font-size: 200px;
  font-size: 12.5rem;
}
@media (min-width: 1440px) {
  .ui-h-extra {
    font-size: 270px;
    font-size: 16.875rem;
  }
}
@media (max-width: 1259px) {
  .ui-h-extra {
    font-size: 144px;
    font-size: 9rem;
  }
}

h1, .h1, .ui-h1, .page-title {
  font-size: 120px;
  font-size: 7.5rem;
  margin-top: 0;
  line-height: 0.95;
}
@media (min-width: 1440px) {
  h1, .h1, .ui-h1, .page-title {
    font-size: 140px;
    font-size: 8.75rem;
  }
}
@media (max-width: 1259px) {
  h1, .h1, .ui-h1, .page-title {
    font-size: 100px;
    font-size: 6.25rem;
  }
}
@media (max-width: 1259px) {
  h1, .h1, .ui-h1, .page-title {
    font-size: 100px;
    font-size: 6.25rem;
  }
}
@media (max-width: 959px) {
  h1, .h1, .ui-h1, .page-title {
    font-size: 56px;
    font-size: 3.5rem;
    line-height: 1.1;
  }
}
@media (max-width: 599px) {
  h1, .h1, .ui-h1, .page-title {
    font-size: 36px;
    font-size: 2.25rem;
    line-height: 1.14;
  }
}

h2, .h2, .ui-h2 {
  font-size: 52px;
  font-size: 3.25rem;
  line-height: 1.2;
}
@media (max-width: 959px) {
  h2, .h2, .ui-h2 {
    font-size: 40px;
    font-size: 2.5rem;
  }
}
@media (max-width: 599px) {
  h2, .h2, .ui-h2 {
    font-size: 32px;
    font-size: 2rem;
  }
}

h3, .h3, .ui-h3 {
  font-size: 42px;
  font-size: 2.625rem;
  line-height: 1.2;
  font-weight: 600;
}
@media (max-width: 959px) {
  h3, .h3, .ui-h3 {
    font-size: 36px;
    font-size: 2.25rem;
  }
}
@media (max-width: 599px) {
  h3, .h3, .ui-h3 {
    font-size: 28px;
    font-size: 1.75rem;
  }
}

h4, .h4, .ui-h4 {
  font-size: 42px;
  font-size: 2.625rem;
  line-height: 1.3;
}
@media (max-width: 959px) {
  h4, .h4, .ui-h4 {
    font-size: 32px;
    font-size: 2rem;
  }
}
@media (max-width: 599px) {
  h4, .h4, .ui-h4 {
    font-size: 24px;
    font-size: 1.5rem;
  }
}

h5, .h5 {
  font-size: 32px;
  font-size: 2rem;
}
@media (max-width: 959px) {
  h5, .h5 {
    font-size: 24px;
    font-size: 1.5rem;
  }
}
@media (max-width: 599px) {
  h5, .h5 {
    font-size: 20px;
    font-size: 1.25rem;
  }
}

h6, .h6, .ui-h5 {
  font-size: 18px;
  font-size: 1.125rem;
  text-transform: none;
  font-weight: 700;
}

.ui-font-midi {
  font-size: 16px;
  font-size: 1rem;
}

.ui-font-mini {
  font-size: 14px;
  font-size: 0.875rem;
}

.h20 {
  font-size: 20px;
  font-size: 1.25rem;
}

.ui-strong,
.ui-bold {
  font-weight: 700;
}

.ui-demi {
  font-weight: 600;
}

.ui-side-title {
  font-size: 54px;
  font-size: 3.375rem;
  color: #F08322;
  margin-bottom: 80px;
  max-width: 480px;
  line-height: 1.1;
  font-weight: 600;
}
@media (max-width: 1439px) {
  .ui-side-title {
    font-size: 42px;
    font-size: 2.625rem;
    line-height: 1.14;
  }
}
@media (max-width: 1259px) {
  .ui-side-title {
    margin-bottom: 60px;
  }
}
@media (max-width: 959px) {
  .ui-side-title {
    font-size: 36px;
    font-size: 2.25rem;
    max-width: 100%;
    margin-bottom: 24px;
  }
}
@media (max-width: 599px) {
  .ui-side-title {
    font-size: 28px;
    font-size: 1.75rem;
  }
}

a {
  color: #F08322;
  text-decoration: none;
  outline: none;
  transition: color 0.3s, border 0.3s;
}
@media (min-width: 960px) {
  a:hover {
    text-decoration: none;
    color: #f39e52;
  }
  a:active {
    color: #f5ae6e;
  }
}

b, strong {
  font-weight: 700;
}

img {
  max-width: 100%;
  height: auto;
}

hr {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 0;
  height: 1px;
  border: 0;
  border-top: 1px solid #dbdbdb;
  overflow: hidden;
}

table {
  border-collapse: collapse;
}

.wrapper a img {
  border: none;
  display: block;
  line-height: 0;
  font-size: 0;
}

.sprite,
.svg-sprite {
  display: block;
  max-width: 100%;
  max-height: 100%;
  -webkit-transition: fill 0.4s, stroke 0.4s;
  -moz-transition: fill 0.4s, stroke 0.4s;
  -ms-transition: fill 0.4s, stroke 0.4s;
  -o-transition: fill 0.4s, stroke 0.4s;
  transition: fill 0.4s, stroke 0.4s;
}

.svg-inline {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  top: -1px;
  margin-right: 5px;
}

/*---- ICONS ---- */
::selection {
  background: #F08322;
  color: #FFF;
}

::-moz-selection {
  background: #F08322;
  color: #FFF;
}

@media (min-width: 992px) {
  ::-webkit-scrollbar {
    background: #CCC;
    width: 8px;
    height: 8px;
  }

  ::-webkit-scrollbar-thumb {
    background: #384b66;
    width: 8px;
    height: 8px;
  }

  ::-webkit-scrollbar-thumb:hover {
    background: #2b3a4f;
  }
}
.ui-backgrounded {
  display: block;
  position: relative;
  overflow: hidden;
  -webkit-background-size: cover;
  background-size: cover;
  background-position: center;
}
.ui-backgrounded.backgrounded .ui-backgrounded-bg {
  visibility: hidden;
  opacity: 0;
  position: absolute;
}

.ui-breadcrumbs {
  position: relative;
  clear: both;
  background: none;
  color: #FFF;
  margin: 0px 0px 35px;
  font-size: 16px;
  font-size: 1rem;
}
.ui-breadcrumbs a {
  color: #FFF;
  border-bottom: 0;
  text-decoration: underline;
}
.ui-breadcrumbs a:hover {
  color: #F08322;
  text-decoration: none;
}
.ui-breadcrumbs a:not(:last-child):after {
  content: "/";
  display: inline-block;
  vertical-align: baseline;
  margin-right: 2px;
  margin-left: 5px;
  position: relative;
  color: #FFF;
}

ul.ui-marked, .node-inner ul {
  margin: 0px 0px 32px;
  list-style: none;
}
ul.ui-marked li, .node-inner ul li {
  padding-left: 24px;
  margin-bottom: 15px;
  position: relative;
}
ul.ui-marked li:before, .node-inner ul li:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0.5em;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #F08322;
}
@media (max-width: 599px) {
  ul.ui-marked li, .node-inner ul li {
    margin-bottom: 10px;
  }
}

ol.ui-marked {
  padding-left: 0px;
  counter-reset: myCounter;
  margin-bottom: 20px;
}
ol.ui-marked > li {
  margin-bottom: 8px;
  position: relative;
  list-style: none;
}
ol.ui-marked > li:before {
  counter-increment: myCounter;
  content: counter(myCounter) ".";
  color: #FFF;
  font-weight: 700;
  margin-right: 7px;
}

.not-styled ul,
.content .not-styled ul,
ul.not-styled,
.content ul.not-styled {
  margin-bottom: 0;
}
.not-styled ul li,
.content .not-styled ul li,
ul.not-styled li,
.content ul.not-styled li {
  padding-left: 0;
  margin-bottom: 0;
  counter-reset: none;
}
.not-styled ul li:before,
.content .not-styled ul li:before,
ul.not-styled li:before,
.content ul.not-styled li:before {
  content: none;
}

article, .article, .seo {
  font-size: 16px;
  font-size: 1rem;
  line-height: 1.375;
  font-weight: 400;
}
article p, .article p, .seo p {
  margin: 0px 0px 32px;
}

.ui-table-overflow::-webkit-scrollbar {
  display: none;
  width: 0;
  height: 0;
  -webkit-appearance: none;
}
@media (max-width: 959px) {
  .ui-table-overflow {
    max-width: 100%;
    max-width: calc(100% + 30px);
    margin-right: -15px;
    margin-left: -15px;
    overflow-x: auto;
    scrollbar-width: 0;
    padding-right: 15px;
    padding-left: 15px;
    -webkit-overflow-scrolling: touch;
  }
  .ui-table-overflow table {
    min-width: 100%;
    width: auto;
  }
}

.ui-table, .node-inner table {
  width: 100%;
  margin: 0px 0px 32px;
  font-size: 18px;
  font-size: 1.125rem;
  vertical-align: top;
}
.ui-table tr, .node-inner table tr {
  vertical-align: top;
}
.ui-table th, .node-inner table th {
  text-align: left;
  color: #758695;
  font-size: 18px;
  font-size: 1.125rem;
  padding: 4px 25px 4px 0px;
  font-weight: 400;
  border-top: 1px solid #97AAB8;
  border-bottom: 1px solid #97AAB8;
}
.ui-table th:last-child, .node-inner table th:last-child {
  padding-right: 0;
}
.ui-table td, .node-inner table td {
  border-top: 1px solid #97AAB8;
  border-bottom: 1px solid #97AAB8;
  padding: 10px 25px 10px 0px;
}
.ui-table td:last-child, .node-inner table td:last-child {
  padding-right: 0;
}
.ui-table tr:first-child th, .node-inner table tr:first-child th, .ui-table tr:first-child td, .node-inner table tr:first-child td {
  border-top: 0;
}
.ui-table tr:last-child th, .node-inner table tr:last-child th, .ui-table tr:last-child td, .node-inner table tr:last-child td {
  border-bottom: 0;
}
@media (max-width: 959px) {
  .ui-table, .node-inner table {
    font-size: 20px;
    font-size: 1.25rem;
  }
  .ui-table th, .node-inner table th {
    font-size: 16px;
    font-size: 1rem;
  }
}
@media (max-width: 599px) {
  .ui-table, .node-inner table {
    font-size: 16px;
    font-size: 1rem;
  }
  .ui-table th, .node-inner table th, .ui-table td, .node-inner table td {
    min-width: 125px;
  }
  .ui-table th, .node-inner table th {
    padding: 4px 20px 4px 0;
    font-size: 14px;
    font-size: 0.875rem;
  }
  .ui-table td, .node-inner table td {
    padding: 8px 20px 8px 0;
  }
}

blockquote {
  margin: 24px 0;
  background: #FFF;
  padding: 35px 45px;
  text-align: left;
  border-left: 9px solid #F08322;
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.05);
  font-size: 20px;
  font-size: 1.25rem;
  font-weight: 400;
}
blockquote p:last-child, blockquote ul:last-child, blockquote ol:last-child, blockquote li:last-child {
  margin-bottom: 0;
}
@media (max-width: 599px) {
  blockquote {
    font-size: 16px;
    font-size: 1rem;
    padding: 25px 20px;
    border-left-width: 3px;
  }
}

figcaption {
  font-size: 18px;
  padding-top: 5px;
}
@media (max-width: 1439px) {
  figcaption {
    font-size: 14px;
    font-size: 0.875rem;
  }
}

/* form, buttons */
button,
input,
optgroup,
select,
textarea {
  font-family: "TT Hoves";
  font-weight: 400;
  outline: none !important;
}

.ui-input,
input.ui-input,
textarea.ui-input,
select.ui-input {
  display: block;
  width: 100%;
  height: 76px;
  max-width: 100%;
  font-family: "TT Hoves";
  background: transparent;
  color: #FFF;
  border: 1px solid #D4D4D4;
  padding: 0px 25px;
  font-size: 18px;
  font-size: 1.125rem;
  -webkit-border-radius: 8px;
  border-radius: 8px;
  outline: none !important;
  -webkit-transition: border 0.3s, box-shadow 0.4s;
  -moz-transition: border 0.3s, box-shadow 0.4s;
  -ms-transition: border 0.3s, box-shadow 0.4s;
  -o-transition: border 0.3s, box-shadow 0.4s;
  transition: border 0.3s, box-shadow 0.4s;
}
.ui-input.wide,
input.ui-input.wide,
textarea.ui-input.wide,
select.ui-input.wide {
  width: 100%;
}
.ui-input.mini,
input.ui-input.mini,
textarea.ui-input.mini,
select.ui-input.mini {
  height: 72px;
}
.ui-input.big,
input.ui-input.big,
textarea.ui-input.big,
select.ui-input.big {
  height: 70px;
}
.ui-input:hover,
input.ui-input:hover,
textarea.ui-input:hover,
select.ui-input:hover {
  border-color: #aeaeae;
  /*box-shadow: $shadow;*/
}
.ui-input:focus,
input.ui-input:focus,
textarea.ui-input:focus,
select.ui-input:focus {
  border-color: #F08322;
  /*box-shadow: $shadow_focus;*/
}
.ui-input:disabled,
input.ui-input:disabled,
textarea.ui-input:disabled,
select.ui-input:disabled {
  background: #f4f4f4;
  border: 1px solid #D4D4D4;
}
.ui-input:disabled:hover, .ui-input:disabled:focus,
input.ui-input:disabled:hover,
input.ui-input:disabled:focus,
textarea.ui-input:disabled:hover,
textarea.ui-input:disabled:focus,
select.ui-input:disabled:hover,
select.ui-input:disabled:focus {
  border-color: #D4D4D4;
}
.ui-input[readonly],
input.ui-input[readonly],
textarea.ui-input[readonly],
select.ui-input[readonly] {
  border: 1px solid #D4D4D4;
  background: #fbfbfb;
}
.ui-input[readonly]:hover, .ui-input[readonly]:focus,
input.ui-input[readonly]:hover,
input.ui-input[readonly]:focus,
textarea.ui-input[readonly]:hover,
textarea.ui-input[readonly]:focus,
select.ui-input[readonly]:hover,
select.ui-input[readonly]:focus {
  border-color: #D4D4D4;
}
.ui-input.no-border,
input.ui-input.no-border,
textarea.ui-input.no-border,
select.ui-input.no-border {
  border: 1px solid transparent;
}
.ui-input.invalid,
input.ui-input.invalid,
textarea.ui-input.invalid,
select.ui-input.invalid {
  color: #97B446;
}

textarea.ui-input {
  display: block;
  padding: 10px 15px;
  border: 1px solid #D4D4D4;
  width: 100%;
  height: auto;
  resize: none;
}

.ui-input-wrp {
  position: relative;
}
.ui-input-wrp input {
  padding-right: 60px;
}

.invalid {
  border-color: #97B446 !important;
  color: #97B446 !important;
}
.invalid * {
  border-color: #97B446 !important;
  color: #97B446 !important;
}

.valid {
  border-color: #25a670 !important;
}
.valid * {
  border-color: #25a670 !important;
}

label.invalid,
span.invalid {
  display: inline-block;
  color: #97B446;
  font-size: 13px;
  font-size: 0.8125rem;
  padding-bottom: 0;
  margin-bottom: 0;
}

span.invalid {
  padding-top: 3px;
  padding-left: 25px;
}

.ui-field {
  position: relative;
}
.ui-field span.invalid {
  position: absolute;
  left: 0;
  top: 100%;
}
.ui-field .iti {
  display: block;
  width: 100%;
}

::-webkit-input-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
}

::-moz-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
}

:-moz-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
}

:-ms-input-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
}

:focus::-webkit-input-placeholder {
  opacity: 0;
}

:focus::-moz-placeholder {
  opacity: 0;
}

:focus:-moz-placeholder {
  opacity: 0;
}

:focus:-ms-input-placeholder {
  opacity: 0;
}

.invalid::-webkit-input-placeholder {
  color: #97B446;
}

.invalid::-moz-placeholder {
  color: #97B446;
}

.invlaid:-moz-placeholder {
  color: #97B446;
}

.invalid:-ms-input-placeholder {
  color: #97B446;
}

.valid::-webkit-input-placeholder {
  color: #25a670;
}

.valid::-moz-placeholder {
  color: #25a670;
}

.vlaid:-moz-placeholder {
  color: #25a670;
}

.valid:-ms-input-placeholder {
  color: #25a670;
}

.ui-btn {
  display: inline-block;
  vertical-align: middle;
  max-width: 100%;
  height: 76px;
  line-height: 74px;
  white-space: nowrap;
  text-align: center;
  padding: 0px 30px;
  border-radius: 8px;
  background-color: transparent;
  border: 1px solid #FFF;
  font-family: "TT Hoves";
  color: #FFF;
  text-transform: none;
  font-size: 24px;
  font-size: 1.5rem;
  font-weight: 400;
  -ms-transition: border 0.3s, color 0.3s;
  transition: border 0.3s, color 0.3s;
  cursor: pointer;
  outline: none;
}
.ui-btn svg.ui-btn-svg {
  display: inline-block;
  vertical-align: middle;
  margin-right: 8px;
  width: 18px;
  height: 18px;
  position: relative;
  top: -2px;
}
@media (min-width: 960px) {
  .ui-btn:hover {
    background-color: transparent;
    border-color: #F08322;
    color: #F08322;
    text-decoration: none;
  }
  .ui-btn:hover svg.ui-btn-arrow {
    stroke: #F08322;
  }
  .ui-btn:active {
    border-color: #f39e52;
    color: #f39e52;
  }
}
.ui-btn-arrow {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  top: -1px;
  width: 24px;
  height: 24px;
  transition: transform 0.15s, stroke 0.3s;
  stroke: #FFF;
  fill: none;
  margin-left: 30px;
}
.ui-btn-block {
  display: block;
  width: 100%;
  margin-left: 0 !important;
  margin-bottom: 10px;
  padding: 0px 5px !important;
}
.ui-btn-big {
  height: 70px;
  line-height: 68px;
  padding: 0px 60px;
}
.ui-btn-bordered {
  background-color: transparent;
  border: 1px solid #FFF;
  line-height: 74px;
  color: #FFF;
}
@media (min-width: 960px) {
  .ui-btn-bordered:hover {
