.lg-backdrop {
  background: #222E3F;
  opacity: 0;
  transform: scale(0.75);
  transition-property: opacity, transform;
}
.lg-backdrop.in {
  opacity: 1;
  transform: scale(1);
}

.lg-toolbar {
  background: transparent;
}
.lg-toolbar .lg-icon {
  font-size: 40px;
  height: 32px;
  line-height: 32px;
  width: 32px;
  color: #FFF;
  padding: 0;
  position: absolute;
  top: 50px;
  right: 50px;
  transition: border 0.3s, color 0.3s, transform 0.3s, opacity 0.3s;
}
.lg-toolbar .lg-icon:hover {
  color: #F08322;
}

.lg-actions .lg-icon {
  border: 2px solid #FFF;
  width: 70px;
  height: 70px;
  border-radius: 35px;
  color: #FFF;
  transition: border 0.3s, color 0.3s, transform 0.3s, opacity 0.3s;
}
.lg-actions .lg-icon:after, .lg-actions .lg-icon:before {
  font-size: 36px;
}
@media (min-width: 960px) {
  .lg-actions .lg-icon:hover {
    color: #F08322;
    border-color: #F08322;
  }
}
.lg-actions .lg-prev {
  left: 50px;
  transform: translate3d(-30px, 0, 0);
  opacity: 0;
}
.lg-actions .lg-next {
  right: 50px;
  transform: translate3d(30px, 0, 0);
  opacity: 0;
}
@media (max-width: 959px) {
  .lg-actions .lg-prev,
.lg-actions .lg-next {
    display: none;
  }
}

.lg-close {
  opacity: 0;
  transform: translate3d(30px, 0, 0);
}

#lg-counter {
  font-size: 28px;
  font-size: 1.75rem;
  color: #FFF;
  position: absolute;
  left: 50px;
  top: 50px;
  padding: 0;
  transition: 0.3s opacity, 0.3s transform;
  transform: translate3d(-30px, 0, 0);
  opacity: 0;
}

.lg-appear .lg-prev,
.lg-appear .lg-next,
.lg-appear .lg-close,
.lg-appear #lg-counter {
  opacity: 1;
  transform: translate3d(0px, 0, 0);
}

.lg-outer .lg-img-wrap {
  padding: 50px 160px;
}

@media (max-width: 959px) {
  .lg-outer {
    background: #000;
  }
  .lg-outer .lg-img-wrap {
    padding: 50px 0px;
  }

  .lg-toolbar {
    background: #222E3F;
    display: flex;
    padding: 18px 15px;
    justify-content: space-between;
    align-items: center;
    flex-direction: row-reverse;
  }
  .lg-toolbar:before, .lg-toolbar:after {
    content: none;
  }
  .lg-toolbar .lg-icon {
    position: relative;
    top: 0;
    right: 0;
    width: 24px;
    height: 24px;
    font-size: 32px;
    line-height: 24px;
    text-align: center;
    float: none;
  }
  .lg-toolbar .lg-icon:after {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  #lg-counter {
    position: static;
    font-size: 18px;
    font-size: 1.125rem;
    font-weight: 400;
  }

  .lg-hide-items .lg-toolbar {
    transform: none;
    opacity: 1;
  }
}
@media (max-width: 599px) {
  .lg-toolbar {
    padding: 10px 15px;
  }

  #lg-counter {
    font-size: 16px;
    font-size: 1rem;
  }
}
.swiper-button-prev, .swiper-button-next {
  position: relative;
  left: auto;
  right: auto;
  top: auto;
  bottom: auto;
  border-radius: 50%;
  width: 64px;
  height: 64px;
  outline: none !important;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.swiper-button-prev:before, .swiper-button-next:before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  opacity: 0;
  transition: opacity 0.4s;
  background-size: contain !important;
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 960px) {
  .swiper-button-prev:hover:before, .swiper-button-next:hover:before {
    opacity: 1;
  }
}
.swiper-button-prev.swiper-button-white:before, .swiper-button-next.swiper-button-white:before {
  filter: brightness(10);
}
.swiper-button-prev.swiper-button-white.swiper-button-prev, .swiper-button-next.swiper-button-white.swiper-button-prev {
  background-image: url(../images/svg/slider-arrow-left_orange.svg);
}
.swiper-button-prev.swiper-button-white.swiper-button-next, .swiper-button-next.swiper-button-white.swiper-button-next {
  background-image: url(../images/svg/slider-arrow-right_orange.svg);
}
.swiper-button-prev:before {
  background-image: url(../images/svg/slider-arrow-left_orange.svg);
}
.swiper-button-next:before {
  background-image: url(../images/svg/slider-arrow-right_orange.svg);
}
@media (max-width: 1259px) {
  .swiper-button-prev, .swiper-button-next {
    width: 50px;
    height: 50px;
  }
}
@media (max-width: 959px) {
  .swiper-button-prev, .swiper-button-next {
    width: 32px;
    height: 32px;
  }
}

.ui-chart {
  color: #FFF;
  display: flex;
  padding-top: 20px;
  padding-bottom: 40px;
}
.ui-chart-block {
  margin-bottom: 80px;
}
.ui-chart-header {
  margin-bottom: 20px;
}
.ui-chart-title {
  font-size: 54px;
  font-size: 3.375rem;
  line-height: 1;
  margin-bottom: 8px;
}
.ui-chart-units {
  font-size: 24px;
  font-size: 1.5rem;
  line-height: 1;
}
.ui-chart-bar {
  height: 450px;
  flex: 1 0 auto;
  min-width: 30px;
  max-width: 54px;
  margin-right: 20px;
  margin-right: clamp(10px,1.5vw,24px);
  position: relative;
  text-align: center;
  transition: opacity 0.3s;
}
.ui-chart-bar:last-child {
  margin-right: 0;
}
.ui-chart-bar.darken {
  opacity: 0.25;
}
.ui-chart-bar.active .ui-chart-bar-value {
  transform: scale(1.5);
}
.ui-chart-bar-progress {
  position: absolute;
  left: 0px;
  right: 0px;
  bottom: 0px;
  min-height: 10px;
  border-top: 1px solid #FFF;
}
@media (min-width: 960px) {
  .ui-chart-bar-progress {
    width: 100% !important;
  }
}
.ui-chart-bar-value {
  position: absolute;
  bottom: 100%;
  margin-bottom: 4px;
  left: 0;
  right: 0;
  color: #FFF;
  font-size: 12px;
  font-size: 0.75rem;
  font-weight: 700;
  transition: transform 0.3s;
}
.ui-chart-bar-label {
  position: absolute;
  top: 100%;
  margin-top: 8px;
  left: 0;
  right: 0;
}
.ui-chart-tabs {
  font-size: 0;
  margin-bottom: 20px;
}
.ui-chart-tab {
  display: inline-block;
  vertical-align: middle;
  height: 60px;
  line-height: 58px;
  border: 1px dashed rgba(255, 255, 255, 0.5);
  border-radius: 30px;
  padding: 0px 20px;
  transition: color 0.4s, border 0.4s;
  color: #FFF;
  font-size: 24px;
  font-size: 1.5rem;
  margin-right: 16px;
  margin-bottom: 15px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.ui-chart-tab:hover, .ui-chart-tab.selected {
  border-style: solid;
  border-color: #FFF;
  color: #FFF;
}
.ui-chart-tab-orange:hover, .ui-chart-tab-orange.selected {
  border-color: #F08322;
  color: #F08322;
}
.ui-chart-tab-green:hover, .ui-chart-tab-green.selected {
