.langs-toggler {
  position: relative;
  display: block;
  min-width: 60px;
  max-width: 90px;
  height: 44px;
  line-height: 42px;
  border: 1px solid #FFF;
  border-radius: 22px;
  padding: 0px 26px 0px 12px;
  font-size: 18px;
  font-size: 1.125rem;
  color: #FFF;
  transition: border 0.3s, color 0.3s;
  cursor: pointer;
}
.langs-toggler-mobile {
  display: none;
  visibility: hidden;
}
.langs-toggler-circle {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin-left: 16px;
}
.langs-toggler-txt {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.langs-toggler-arrow {
  position: absolute;
  right: 12px;
  top: 50%;
  margin-top: -4px;
  width: 10px;
  height: 8px;
  background: url(../images/svg/childs-toggler-orange.svg) center no-repeat;
  background: url(../images/svg/childs-toggler.svg) center no-repeat;
  background-size: contain;
  transition: transform 0.3s, background 0.3s;
}
.langs-toggler-drop {
  position: absolute;
  z-index: 99;
  right: 0;
  top: 100%;
  margin-top: 30px;
  min-width: 185px;
  background: #1D2735;
  padding: 40px 40px 24px;
  white-space: nowrap;
  line-height: 1.1;
  font-size: 18px;
  font-size: 1.125rem;
  opacity: 0;
  visibility: hidden;
  transform: translate(0px, -10px);
  transition: visibility 0.3s, opacity 0.3s, transform 0.3s;
}
.langs-toggler-drop:before {
  content: "";
  position: absolute;
  right: 17px;
  bottom: 100%;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 10px 10px 10px;
  border-color: transparent transparent #1D2735 transparent;
}
.langs-toggler-drop.opened {
  opacity: 1;
  visibility: visible;
  transform: translate(0px, 0px);
}
.langs-toggler-lang {
  display: block;
  margin-bottom: 16px;
  color: #FFF;
}
.langs-toggler-lang:hover, .langs-toggler-lang.active {
  color: #F08322;
}
.langs-toggler.opened .langs-toggler-arrow {
  transform: rotate(180deg);
}
